<template>
  <nav class="nav" @mouseover="hoverNavDown">
    <v-row class="nav_con">
      <v-col class="nav_logo" cols="4">
        <img src="../../assets/logo/whitelogo.svg" alt="logo" class="blogo" />
      </v-col>
      <v-spacer />
      <v-col cols="8" class="menu_con">
        <div class="menu_list">
          <template v-for="(item, i) in menus" :key="i">
            <router-link :to="item.value">
              <v-list-item :value="i">
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </router-link>
          </template>
        </div>
      </v-col>
      <v-col cols="8" class="small_nav">
        <div class="ham" ref="hamElement" @click="menu_state = !menu_state">
          <div class="bar_con">
            <span class="bar1"></span>
            <span class="bar2"></span>
            <span class="bar3"></span>
          </div>
        </div>
      </v-col>
    </v-row>
  </nav>
  <ScrollNav @mouseleave="hoverNavUp" />
</template>

<script>
import { reactive } from "vue";
import ScrollNav from "../header/ScrollNav.vue";

export default {
  name: "HeaderNav",
  components: {
    ScrollNav,
  },
  data() {
    return {
      nav_state: false,
      lastScrollPosition: 0,
    };
  },

  setup() {
    const state = reactive({
      menus: [
        {
          title: "회사소개",
          value: "/Intro",
        },
        {
          title: "회사소식",
          value: "/News",
        },
        {
          title: "서비스",
          value: "/Service",
        },
        {
          title: "문의&의뢰",
          value: "/Contact",
        },
        {
          title: "채용",
          value: "/Employ",
        },
      ],
    });

    return state;
  },
  mounted() {
    const miscalc = 3; // 오차 범위

    window.addEventListener("wheel", (e) => {
      if (e.wheelDeltaY > 0 && window.scrollY + miscalc <= window.innerHeight) {
        this.scrollNavUp();
      }
      if (e.wheelDeltaY < 0) {
        this.scrollNavDown();
      }
    });

    window.addEventListener("scroll", this.handleScroll);

    this.animateNav();
    this.$nextTick(() => {
      const hamElement = this.$refs.hamElement;

      hamElement.addEventListener("click", function () {
        this.classList.toggle("active");
      });
    });
  },

  methods: {
    animateNav() {
      this.$anime({
        targets: [".nav"],
        top: "0px",
        duration: 200,
        easing: "easeInSine",
      });
    },

    scrollNavDown() {
      const nav_down = document.querySelector(".scrollNav");
      nav_down.style.top = "0%";
      nav_down.style.transition = "all .5s ease";
    },
    scrollNavUp() {
      if (window.innerHeight >= window.scrollY) {
        const nav_down = document.querySelector(".scrollNav");
        nav_down.style.top = "-9%";
        nav_down.style.transition = "all .5s ease";
      }
    },
    hoverNavDown() {
      if (window.scrollY === 0) {
        this.scrollNavDown();
      }
    },
    hoverNavUp() {
      if (window.scrollY === 0) {
        const nav_down = document.querySelector(".scrollNav");
        nav_down.style.top = "-9%";
        nav_down.style.transition = "all .5s ease";
      }
    },
    handleScroll() {
      const scrollPosition =
        window.pageYOffset || document.documentElement.scrollTop;

      if (scrollPosition > this.lastScrollPosition) {
        this.isScrollingDown = true;
      } else {
        this.isScrollingDown = false;
      }

      this.lastScrollPosition = scrollPosition;
      this.mobileHeader(this.isScrollingDown);
    },
    mobileHeader(isScrolling) {
      if (isScrolling) {
        this.scrollNavDown();
      } else if (!isScrolling) {
        this.scrollNavUp();
      }
    },
  },
};
</script>

